<template>
    <ul :class="['node-tree', 'tree-level-' + level]">
        <li v-for="(item, key) in data" :key="key" class="tree-item">
            <h4
                :class="{
                    'node-name': true,
                    'actived': getNodeActivedState(item.id)
                }"
                @click.stop="onSelectNode(item, true)">
                <i :class="item.pipeline ? 'common-icon-node-subflow' : 'common-icon-node-tasknode'"></i>
                <span class="name" :title="item.name">{{item.name}}</span>
            </h4>
            <NodeTree
                v-if="item.pipeline"
                class="sub-tree"
                :data="item.pipeline.activities"
                :selectedFlowPath="selectedFlowPath"
                :heirarchy="heirarchy ? `${heirarchy}.${item.id}` : item.id"
                :level="level + 1"
                @onSelectNode="onSelectNode">
            </NodeTree>
        </li>
    </ul>
</template>
<script>
import '@/utils/i18n.js'
export default {
    name: 'NodeTree',
    props: {
        data: {
            type: Object,
            default () {
                return {}
            }
        },
        selectedFlowPath: {
            type: Array,
            default () {
                return []
            }
        },
        heirarchy: {
            type: String,
            default: ''
        },
        level: {
            type: Number,
            default: 1
        }
    },
    methods: {
        getNodeActivedState (id) {
            const len = this.selectedFlowPath.length
            if (this.selectedFlowPath[len - 1].id === id) {
                return true
            }
            return false
        },
        onSelectNode (node, isClick, type) {
            let nodeHeirarchy = node
            const nodeType = node.pipeline ? 'subflow' : 'tasknode'
            if (isClick) {
                nodeHeirarchy = this.heirarchy ? `${this.heirarchy}.${node.id}` : String(node.id)
            }
            this.$emit('onSelectNode', nodeHeirarchy, false, nodeType)
        }
    }
}
</script>
<style lang="scss" scoped>
@import '@/scss/config.scss';
@import '@/scss/mixins/scrollbar.scss';
.node-tree {
    display: inline-block;
    width: 100%;
    &.tree-level-1 {
        overflow-x: auto;
        @include scrollbar;
    }
    &.sub-tree {
        .tree-item {
            position: relative;
            margin-left: 20px;
            &:last-child:before {
                height: 22px;
            }
            &:before {
                content: '';
                position: absolute;
                left: 8px;
                top: -6px;
                height: 100%;
                border-left: 1px dashed $commonBorderColor;
            }
            &:after {
                content: '';
                position: absolute;
                left: 12px;
                top: 16px;
                width: 8px;
                border-bottom: 1px dashed $commonBorderColor;
            }
        }
    }
    .node-name {
        display: inline-block;
        margin: 0;
        padding-left: 20px;
        height: 30px;
        line-height: 30px;
        font-size: 12px;
        font-weight: normal;
        color: $greyDefault;
        white-space: nowrap;
        cursor: pointer;
        &.actived {
            color: $blueDefault;
        }
        &:hover {
            color: $blueDefault;
        }
        i {
            font-size: 16px;
            font-weight: bold;
            vertical-align: -3px;
        }
        .name {
            display: inline-block;
            max-width: 85px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            vertical-align: -10px;
        }
    }
}
</style>


